<template>
	<div id="paotui-list">
		<public-header title="跑腿订单列表"></public-header>
		<public-footer :showPreLoading="showPreLoading" :menufooter="menufooter"></public-footer>
		<div class="content">
			<van-tabs
					v-model="tabActive"
					@change="onChangeStatus"
					color="#0EC3B3"
					title-active-color="#0EC3B3"
					title-inactive-color="#000"
					:line-width="80"
					:line-height="2"
					:offset-top="46"
					sticky
					swipeable
			>
				<van-tab v-for="(item, index) in tabs" :key="index">
					<div slot="title" class="tab-title">
						{{item.status_cn}} <span class="order-num" v-if="item.num > 0">{{item.num}}</span>
					</div>
					<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
						<van-list
								v-model="records.loading"
								:finished="records.finished"
								@load="onLoad"
								:offset="100"
								:immediate-check="false"
								v-if="!records.empty"
						>
							<div class="order-item" v-for="(item, index) in records.data" :key="item.id">
								<div class="van-hairline--bottom flex-lr padding-15-tb padding-10-lr" @click="onDetail(item.id)">
									<div class="flex">
										<span class="c-primary font-500 font-18">#{{item.id}}</span>
										<div class="distance flex">
											<div class="img-wrap">
												<img class="w-100" src="static/img/start.png" alt="">
											</div>
											<div class="text">-{{item.store2deliveryer_distance}}km-</div>
											<div class="img-wrap">
												<img class="w-100" src="static/img/ds.png" alt="">
											</div>
											<div class="text">-{{item.store2user_distance}}km-</div>
											<div class="img-wrap">
												<img class="w-100" src="static/img/end.png" alt="">
											</div>
										</div>
									</div>
									<span class="c-danger font-15 font-500">{{Lang.dollarSign}} {{item.deliveryer_total_fee}}</span>
								</div>
								<div class="padding-10-lr margin-15-t">
									<div class="flex-lr">
										<ul class="order-tags">
											<li class="handle-type">{{item.delivery_collect_type_cn}}</li>
											<li v-if="0">保温服务</li>
										</ul>
										<div class="c-danger flex" v-if="item.status == 2 && item.delivery_status != 1">
											<template v-if="item.delivery_overtime > 0">
												距超时<count-down :endTime="item.delivery_overtime" class="c-danger"></count-down>
											</template>
											<template v-else-if="item.delivery_overtime_start > 0">
												已超时<count-down :startTime="item.delivery_overtime_start" class="c-danger"></count-down>
											</template>
										</div>
									</div>
								</div>
								<ul class="address padding-10-lr" @click="onDetail(item.id)">
									<li class="flex-lr margin-15-t">
										<span class="c-primary font-15">物:</span>
										<ul class="address-text">
											<li class="font-15 font-500">{{item.goods_name}}</li>
										</ul>
									</li>
									<li class="flex-lr margin-15-t">
										<span class="c-info font-15">{{item.order_type == 'buy' ? '买' : '取'}}:</span>
										<ul class="address-text">
											<li class="font-15 font-500">{{item.buy_address}}</li>
										</ul>
									</li>
									<li class="flex-lr margin-15-t">
										<span class="c-primary font-15">送:</span>
										<ul class="address-text">
											<li class="font-15 font-500 ">{{item.accept_address}}</li>
										</ul>
									</li>
								</ul>
								<div class="note" v-if="item.note">
									<div class="note-title">备注:</div>
									<div class="note-text">{{item.note}}</div>
								</div>
								<ul class="time padding-10-lr margin-15-t flex-lr">
									<li>{{item.addtime_cn}}下单</li>
									<li>配送 {{item.delivery_time}}</li>
								</ul>
								<ul class="btn-group margin-15-t padding-10-lr flex-lr">
									<template v-if="item.delivery_status == 1">
										<li class="btn-item bg-warning" @click="onChangeOrderStatus(index, 'delivery_assign')">抢单</li>
									</template>
									<template v-else-if="item.delivery_status == 2 || item.delivery_status == 3">
										<template v-if="item.transfer_delivery_status == 1">
											<li class="btn-item bg-danger" @click="onChangeOrderStatus(index, 'direct_transfer_refuse')">拒绝转单</li>
											<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'direct_transfer_agree')">接受转单</li>
										</template>
										<template v-else>
											<li class="btn-item bg-warning" v-if="item.data && item.data.yinsihao_status == 1" @click="onCallCustomer(item.id, item.order_sn)">联系顾客</li>
											<li class="btn-item bg-warning" v-else @click="util.jsTel(item.accept_mobile)">联系顾客</li>
											<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'delivery_instore')" v-if="item.delivery_status == 2">确认取货</li>
											<li class="btn-item bg-primary" @click="onChangeOrderStatus(index, 'delivery_success')" v-else>确认送达</li>
										</template>
									</template>
								</ul>
								<div v-if="item.is_pay == 1">
									<div class="have-pay" v-if="item.pay_type != 'delivery'"></div>
									<div class="delivery-pay" v-else-if="item.pay_type == 'delivery'"></div>
								</div>
							</div>
						</van-list>
						<div class="no-data" v-else>
							<img src="static/img/order_no_con.png" alt="">
							<template v-if="filter.items.status == 1">
								<template v-if="deliveryer.work_status == 1">
									<template v-if="deliveryer.is_errander == 1">
										<template v-if="!can_collect_order">
											<p >当前调度模式不允许抢单,请等待管理员或系统派单</p>
										</template>
										<template v-else>
											<p>暂无待抢的跑腿订单</p>
										</template>
									</template>
									<template v-else>
										<p>您没有跑腿单配送权限，请联系管理员</p>
									</template>
								</template>
								<template v-else>
									<p>您当前处于收工状态,不能抢单</p>
								</template>
							</template>
							<template v-else>
								<p>暂无符合条件的跑腿订单</p>
							</template>
						</div>
					</van-pull-refresh>
				</van-tab>
			</van-tabs>
			<failed-tips :failedTips="failedTips" v-if="failedTips.show"></failed-tips>
		</div>
		<van-dialog
			v-model="code.codeShow"
			show-cancel-button
			confirmButtonText="确认送达"
			@confirm="onChangeOrderStatus(code.orderIndex, 'delivery_success')"
			@cancel="code.codeShow = false"
		>	
			<van-field
				placeholder="输入收货码"
				input-align="center"
				readonly
			/>
			<van-field
				v-model="code.value"
				placeholder="请输入收货码"
			/>
		</van-dialog>
		<van-dialog
				v-model="yinsihao.status"
				show-cancel-button
				title="顾客号码保护中"
				confirm-button-text="立即联系"
				@confirm="util.jsTel(yinsihao.secret_mobile)"
		>
			<div class="padding-15-tb text-center font-16">
				<span class="font-bold c-default">{{yinsihao.secret_mobile}}</span>
				<span class="padding-10-lr">转</span>
				<span class="font-bold c-danger">{{yinsihao.extension}}</span>
			</div>
			<div class="padding-15-b font-14 c-gray text-center">播打{{yinsihao.secret_mobile}}接通后，输入{{yinsihao.extension}}#即可</div>
		</van-dialog>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import countDown from "@/components/countDown"
	import {getRecords, getData} from "@/controller/funcCommon"
	import {changeOrderStatus} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			countDown
		},
		data() {
			return {
				tabActive: 0,
				tabs: [
					{
						status: 1,
						status_cn: '待抢',
						num: 0,
					},
					{
						status: 2,
						status_cn: '待取货',
						num: 0,
					},
					{
						status: 3,
						status_cn: '配送中',
						num: 0,
					},
					{
						status: 4,
						status_cn: '已完成',
						num: 0,
					}
				],
				filter: {
					items: {
						status: 1
					}
				},
				isRefresh: false,
				showPreLoading: true,
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				deliveryer: {},
				can_collect_order: false,
				verification_code: false,
				config: {},
				code: {
					codeShow: false,
					value: '',
					orderIndex: -1
				},
				failedTips: {
					show: false,
					type: 'message',
					tips: '您没有跑腿单的配送权限，请联系平台管理员',
					btnText: '',
					link: '',
					position: 'absolute',
				},
				menufooter: {},
				yinsihao: {
					status: false,
					secret_mobile: '',
					extension: ''
				}
			}
		},
		methods: {
			onChangeStatus(index, title) {
				var that = this;
				that.filter.items.status = that.tabs[index]['status'];
				this.onLoad(true);
			},
			onLoad(force){
				var that = this;
				getRecords({
					vue: that,
					force: force,
					url: 'delivery/order/errander/list',
					recordsName: 'orders',
					data: {
						menufooter: 1,
						_deliveryerOrderTabType: 'errander',
					},
					success: function(result) {
						that.deliveryer = result.deliveryer;
						that.can_collect_order = result.can_collect_order;
						that.verification_code = result.verification_code;
						that.config = result.config;
					},
					fail: function() {
						that.failedTips.show = true;
					}
				});
			},

			onChangeOrderStatus(index, type) {
				var that = this;
				if(type == 'delivery_success' && that.verification_code == 1) {
					that.code.codeShow = true;
					that.code.orderIndex = index;
					if(!that.code.value) {
						return;
					}
				}
				changeOrderStatus({
					vue: that,
					type: type,
					index: index,
					from: 'list',
					order: that.records.data[index],
					code: that.code.value
				})
			},

			onDetail(id) {
				if(this.filter.items.status == 1) {
					this.util.$toast('抢单后才能查看详情', '', 1000);
					return;
				}
				this.$router.push(this.util.getUrl({path: 'pages/paotui/detail', query: {id: id}}));
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

			onCallCustomer(order_id, order_sn) {
				var that = this;
				getData({
					vue: that,
					url: 'yinsihao/yinsihao',
					data: {
						order_id: order_id,
						type: 'errander',
						ordersn: order_sn,
						orderType: 'errander'
					},
					success: function(res) {
						that.yinsihao.secret_mobile = res.data.secret_mobile;
						that.yinsihao.extension = res.data.extension;
						that.yinsihao.status = true;
					}
				})
			},
		},
		mounted() {
			var that = this;
			that.onLoad();
			setInterval(function() {
				if(that.filter.items.status == 1 && that.config.auto_refresh == 1) {
					that.onLoad(true);
				}
			}, 15000);

			this.util.imap();
			this.util.icloudapi({
				vue: that,
				page: 'orderPaotui'
			});
		}
	}
</script>

<style>
	#paotui-list .tab-title{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: center;
	}
	#paotui-list .tab-title .order-num{
		display: inline-flex;
		align-items: center;
		justify-content: center;
		min-width: 14px;
		height: 14px;
		padding: 0 5px;
		margin-left: 3px;
		border-radius: 14px;
		color: #fff;
		background-color: #0EC3B3;
		font-size: 12px;
	}
	#paotui-list .order-item{
		background-color: #fff;
		font-size: 14px;
		margin-top: 10px;
		padding-bottom: 15px;
	}
	#paotui-list .order-item:first-child{
		margin-top: 0px;
	}
	#paotui-list .order-item .distance{
		margin-left: 20px;
	}
	#paotui-list .order-item .distance .img-wrap{
		width: 25px;
		font-size: 0px;
	}
	#paotui-list .order-item .order-tags{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		font-size: 12px;
	}
	#paotui-list .order-item .order-tags li{
		padding: 3px 5px;
		border-radius: 3px;
		margin-right: 5px;
		color: #FF931E;
		border: 1px solid #FF931E;
	}
	#paotui-list .order-item .order-tags .handle-type{
		background-color: #FF931E;
		color: #fff;
		font-style: italic;
		padding: 3px 7px 3px 3px;
	}
	#paotui-list .order-item .address-text{
		flex: 1;
		padding-left: 15px;
	}
	#paotui-list .order-item .note{
		margin: 15px 10px 0 10px;
		padding: 10px;
		border-radius: 3px;
		background-color: #DFFBF9;
		color: #16C5B6;
		display: flex;
		align-items: flex-start;
		justify-content: space-between;
	}
	#paotui-list .order-item .note .note-text{
		flex: 1;
		padding-left: 10px;
		line-height: 1.2;
	}
	#paotui-list .order-item .time li{
		color: #1AB5F5;
		border: 1px solid #1AB5F5;
		font-size: 12px;
		padding: 2px 5px;
		border-radius: 3px;
	}
	#paotui-list .order-item .btn-group .btn-item{
		flex: 1;
		height: 44px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 5px;
		font-size: 15px;
	}
	#paotui-list .order-item .btn-group .btn-item:nth-child(2){
		margin-left: 10px;
	}
	#paotui-list .order-item .have-pay{
		position: absolute;
		width: 100px;
		height: 50px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/have-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
	#paotui-list .order-item .delivery-pay{
		position: absolute;
		width: 110px;
		height: 70px;
		left: 50%;
		margin-left: -50px;
		top: 100px;
		background: url("http://1.duoxunwl.com/addons/we7_wmall/static/img/delivery-pay.png") no-repeat center center;
		background-size: 100% 100%;
	}
</style>


